import React from 'react';
import styles from './agents.css';
import AgentList from './List';
import Department from './Department';
import { icons } from '../../constants/enums';

const AgentsLeft = props => (
  <div className={styles.agentsSection}>
    {
      props.isDepartment ?
      props.agentData.map(agent => (
        <Department
          key={agent.id}
          department={agent}
          onDepartmentClick={props.onDepartmentClick}
          onItemClick={props.onItemClick}
        />
      )) :
      <AgentList
        agents={props.agentData}
        onItemClick={props.onItemClick}
      />
    }
  </div>
);

AgentsLeft.propTypes = {
  agentData: React.PropTypes.arrayOf(React.PropTypes.shape({})).isRequired,
  isDepartment: React.PropTypes.bool.isRequired,
  onDepartmentClick: React.PropTypes.func.isRequired,
  onItemClick: React.PropTypes.func.isRequired,
};
AgentsLeft.examples = {
  'Default Example': {
    description: 'Agents',
    component:
  <AgentsLeft
    agentData={[
      {
        name: 'department 1',
        selected: true,
        agents: [
          {
            icon: { type: icons.statusOnline, color: '#329fd9' },
            name: 'Allon (1 chat(s))',
            unreadMessage: 5,
            selected: false,
          },
        ],
      },
      {
        name: 'department 2',
        selected: true,
        agents: [
          {
            icon: { type: icons.statusOnline, color: '#329fd9' },
            name: 'Allon (1 chat(s))',
            unreadMessage: 5,
            selected: false,
          },
        ],
      },
    ]}
    isDepartment
    onItemClick={_ => _}
    onDepartmentClick={_ => _}
  />
        ,
  },
};
export default AgentsLeft;



