import { connect } from 'react-redux';
import _ from 'lodash';
import SendFile from '../../components/Icons/ChatAction/SendFile';
import * as selectors from '../../reducers/selectors';
import * as chatBusiness from '../Business/agentChat';
import { isImage } from '../../utils/seq';
import * as chatActions from '../../actions/agentChat';
import * as agentActions from '../../actions/agent';
import { formatAgentChatUploadUrl } from '../../utils/helper';
import { createGUID } from '../../utils/guid';
import { noSelectedForAgent } from '../../constants/enums';

let previousState = {};
function getUploadUrl(uploadUrl, sessionId, siteId) {
  return formatAgentChatUploadUrl(uploadUrl,
                        sessionId,
                        siteId,
                        1,
                        createGUID());
}
const mapStateToProps = (state) => {
  if (_.isNull(selectors.getSelectedIdForAgents(state)) ||
      selectors.getSelectedIdForAgents(state) === noSelectedForAgent) {
    return {
      chatId: noSelectedForAgent,
      ifDisplay: true,
      disabled: true,
      siteInfo: {},
      files: [],
    };
  }
  const [isAgent, id] = chatBusiness.getAgentOrDepartmentId(state);
  if (!chatBusiness.isActiveStatus(state, isAgent, id)) {
    const newState = Object.assign({}, previousState);
    newState.disabled = true;
    return newState;
  }
  const uploadUrl = selectors.getFileUploadUrl(state);
  const sessionId = selectors.getCurrentAgentSession(state);
  const siteId = selectors.getSiteId(state);
  const sendFileUploadUrl = getUploadUrl(uploadUrl, sessionId, siteId);

  previousState = {
    chatId: id,
    ifDisplay: true,
    disabled: !chatBusiness.ifChatIsAvalable(state, isAgent, id),
    siteInfo: {
      id,
      isAgent,
      sendFileUploadUrl,
      sessionId,
      siteId,
      privateMessage: 1,
      sendToIds: chatBusiness.getSendToIds(state, isAgent, id),
      downloadUrl: selectors.getFileDownloadUrl(state),
      maxFileSize: selectors.getAgentChatFileSize(state),
    },
    files: selectors.getAgentChatUploadFiles(state, id),
  };
  return previousState;
};

const mapDispatchToProps = dispatch => ({
  onStart(id, filename, guid) {
    dispatch(chatActions.startUpload(id, filename, guid));
  },
  onProgress(id, percentage, filename, guid) {
    dispatch(chatActions.uploadFile(id, percentage, filename, guid));
  },
  onSuccess(id, filename, fid, siteInfo, url, guid) {
    dispatch(chatActions.uploadFile(id, 100, filename, fid));
    dispatch(chatActions.appendFile(
      chatBusiness.filterSendToIds(siteInfo.id, siteInfo.sendToIds, siteInfo.isAgent),
      url,
      filename,
      isImage(filename)
    ));
    siteInfo.sendToIds.forEach((agentId) => {
      dispatch(agentActions.sendFileToAgent(agentId, filename, guid));
    });
  },
  onCancel(id, filename, guid) {
    dispatch(chatActions.cancelUpload(id, filename, guid));
  },
  showErrorMessage(chatId, errorMsg) {
    dispatch(chatActions.setErrorMessageForAgentChat(chatId, errorMsg));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(SendFile);



