import React from 'react';
import moment from 'moment';
import Button from 'src/components/Button/Button';
import classnames from 'classnames';
import Icon from '../../Icon/Icon';
import style from './ChatContent.css';
import { icons } from '../../../constants/enums';
import * as audioVideoChatStatus from '../../../constants/enums/visitorAudioVideoChatStatus';
import Avatar from '../../Icons/Avatar';

const statusText = (status) => {
  switch (status) {
    case audioVideoChatStatus.visitorRequestAudioChat:
      return 'Audio Chat';
    case audioVideoChatStatus.agentRequestAudioChat:
      return 'Audio Chat...';
    case audioVideoChatStatus.visitorRequestVideoChat:
      return 'Video Chat';
    case audioVideoChatStatus.agentRequestVideoChat:
      return 'Video Chat...';
    case audioVideoChatStatus.audioChatting:
      return 'Audio Chat';
    default:
      return '';
  }
};

const showAcceptButton = status =>
  status === audioVideoChatStatus.visitorRequestAudioChat
  || status === audioVideoChatStatus.visitorRequestVideoChat;

const showRefuseButton = status =>
  status === audioVideoChatStatus.visitorRequestAudioChat
  || status === audioVideoChatStatus.visitorRequestVideoChat
  || status === audioVideoChatStatus.agentRequestAudioChat
  || status === audioVideoChatStatus.agentRequestVideoChat
  || status === audioVideoChatStatus.audioChatting
  || status === audioVideoChatStatus.videoChatting;

const isVideoChatting = status => status === audioVideoChatStatus.videoChatting;
const isAudioChatting = status => status === audioVideoChatStatus.audioChatting;
const isChatting = status => status === audioVideoChatStatus.audioChatting
  || status === audioVideoChatStatus.videoChatting;
const audioVideoIcon = (status) => {
  if (status === audioVideoChatStatus.audioChatting
    || status === audioVideoChatStatus.agentRequestAudioChat
    || status === audioVideoChatStatus.visitorRequestAudioChat) {
    return icons.requestAudioChat;
  }
  return icons.requestVideoChat;
};

const diffTime = (a, b) => {
  const a1 = a.getTime();
  const b1 = b.getTime();
  if (a1 < b1) {
    return '';
  }

  const d = a1 - b1;
  const dd = Math.floor(d / (24 * 3600 * 1000));
  const hms = d - (dd * 24 * 3600 * 1000);

  return `${dd > 0 ? `${dd}.` : ''}${moment.utc(hms).format((hms < 3600 * 1000 && dd <= 0) ? 'mm:ss' : 'HH:mm:ss')}`;
};

class AudioVideoChatContent extends React.Component {
  constructor(props) {
    super(props);
    if (isChatting(props.audioVideoChatStatus)) {
      this.state = { timespan: diffTime(new Date(), props.audioVideoChatStartTime) };
    } else {
      this.state = { timespan: '' };
    }
  }

  componentWillMount() {
    this.startClock();
  }

  componentDidMount() {
    this.insertVideoObject(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      timespan: diffTime(
        new Date(),
        nextProps.audioVideoChatStartTime
      ),
    });
  }

  componentDidUpdate() {
    this.insertVideoObject(this.props);
  }

  componentWillUnmount() {
    clearInterval(this.clock);
  }

  insertVideoObject(props) {
    const p2pChat = props.p2pChat;
    if (p2pChat != null
      && isVideoChatting(props.audioVideoChatStatus)
      && this.videoContainer) {
      const localVideo = p2pChat.getLocalVideo();
      if (localVideo) {
        localVideo.className = style.localVideo;
        if (document.getElementById('localVideo') == null) {
          localVideo.id = 'localVideo';
          this.videoContainer.appendChild(localVideo);
        }
        localVideo.play();
      }

      const remoteVideo = p2pChat.getRemoteVideo();
      if (remoteVideo) {
        remoteVideo.className = style.remoteVideo;
        if (document.getElementById('remoteVideo') == null) {
          remoteVideo.id = 'remoteVideo';
          this.videoContainer.appendChild(remoteVideo);
        }
        remoteVideo.play();
      }
    }
  }

  startClock() {
    this.clock = setInterval(
      () => this.setState({
        timespan: diffTime(
          new Date(),
          this.props.audioVideoChatStartTime
        ),
      }),
      1000
    );
  }

  render() {
    const props = this.props;
    const videoChatting = isVideoChatting(props.audioVideoChatStatus);
    const audioChatting = isAudioChatting(props.audioVideoChatStatus);
    // console.log('AudioVideoChatContent:', props);
    // console.log('videoChatting:', videoChatting);
    // console.log('statusText', statusText(props.audioVideoChatStatus));
    // console.log('icon:', audioVideoIcon(props.audioVideoChatStatus));
    // console.log('showAcceptButton:', showAcceptButton(props.audioVideoChatStatus));
    // console.log('showRefuseButton:', showRefuseButton(props.audioVideoChatStatus));
    const timespan = this.state.timespan;
    const incoming = showAcceptButton(props.audioVideoChatStatus);
    // console.log('p2pChat:', p2pChat);
    return (<div
      className={
        classnames(
          style.chatVideoContainer, videoChatting && style.videoChatting
        )
      }
    >
      {
        videoChatting &&
        <div
          className={style.videoContainer}
          ref={(element) => { this.videoContainer = element; }}
        />
      }
      <div className={style.chatVideoItem}>
        {
          videoChatting &&
          <div className={style.chattingInfo}>
            <div className={style.time}>{timespan.toString()}</div>
          </div>
        }
        {
          !videoChatting &&
          <div className={style.avatarContainer}>
            <Avatar
              isContactOrUserOpen={props.isContactOrUserOpen}
              avatarLink={props.avatarLink}
              identityType={props.identityType}
              ifShowTooltip={false}
            />
          </div>
        }
        {!videoChatting &&
          <div className={style.visitorName}>{props.chatTo}</div>
        }
        {
          !videoChatting &&
          <div className={style.notice}>
            <Icon type={audioVideoIcon(props.audioVideoChatStatus)} />
            {`${statusText(props.audioVideoChatStatus)}`}
            {audioChatting ? ` ${timespan}` : ''}
          </div>
        }
        <div className={classnames(style.action, incoming ? style.incoming : null)}>
          {
            incoming &&
            <Button className={style.accept} onClick={props.onAcceptClick} type="noborder">
              <Icon type={icons.acceptChat} />
            </Button>
          }
          {
            showRefuseButton(props.audioVideoChatStatus) &&
            <Button
              className={style.refuse}
              type="noborder"
              onClick={props.onRefuseClick}
            >
              <Icon type={icons.refuseChat} />
            </Button>
          }
        </div>
      </div>
    </div >);
  }
}

export default AudioVideoChatContent;

AudioVideoChatContent.propTypes = {
  avatarLink: React.PropTypes.string,
  chatTo: React.PropTypes.string,
  identityType: React.PropTypes.number,
  isContactOrUserOpen: React.PropTypes.bool,
  onAcceptClick: React.PropTypes.func,
  onRefuseClick: React.PropTypes.func,
  /* eslint-disable react/forbid-prop-types */
  audioVideoChatStartTime: React.PropTypes.any,
  audioVideoChatStatus:
  React.PropTypes.oneOf(Object.keys(audioVideoChatStatus).map(k => audioVideoChatStatus[k])),
  p2pChat: React.PropTypes.object,
};



