import React from 'react';
import _ from 'lodash';
import styles from './agents.css';
import AgentItem from './Item';
import { icons } from '../../constants/enums';

class List extends React.Component {
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(nextProps.agents, this.props.agents);
  }
  render() {
    return (
      <div className={styles.listsection}>
        {this.props.agents.map(agent => (
          <AgentItem
            key={agent.id}
            agent={agent}
            onItemClick={this.props.onItemClick}
          />
        ))}
      </div>
    );
  }
}

List.propTypes = {
  agents: React.PropTypes.arrayOf(React.PropTypes.shape({
    icon: React.PropTypes.shape({
      type: React.PropTypes.string,
      color: React.PropTypes.string,
    }),
    name: React.PropTypes.string,
    unreadMessage: React.PropTypes.number,
    selected: React.PropTypes.bool,
  })).isRequired,
  onItemClick: React.PropTypes.func.isRequired,
};
List.examples = {
  'Default Example': {
    description: 'Agent List',
    component:
  <List
    agents={[
      {
        icon: { type: icons.statusOnline, color: '#329fd9' },
        name: 'Allon (1 chat(s))',
        unreadMessage: 5,
        selected: true,
      },
      {
        icon: { type: icons.statusOnline, color: '#329fd9' },
        name: 'Allon2 (1 chat(s))',
        unreadMessage: 3,
        selected: false,
      },
    ]}
    onItemClick={_.identity}
  />
        ,
  },
};
export default List;



